@import "../mixins";
.catalog-parts,
.catalog-part{
  padding-bottom: 1em;
  flex-grow: 1;
}

.car-info{
  padding: 1em 2em 2em;
  background: #FFFFFF;
  border: 1px solid #E4E7EC;
  box-shadow: 0 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06);
  border-radius: .5em;
  margin-top: 1.5em;
  width: calc(100% + 4em);
  margin-left: -2em;
  &__grid{
    margin-top: 1em;
    column-count: 4;
    column-gap: 1.5em;
  }
  &__group{
    display: inline-block;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 1em;
    }
    .text14{
      line-height: 143%;
    }
    .text16{
      line-height: 188%;
      margin-top: .5em;
    }
  }
}

.catalog-part{
  &__wrap{
    display: grid;
    grid-template-columns: auto 42.75em;
    justify-content: space-between;
    margin-top: 1.5em;
    gap: 1em;
  }
  &__items{
    @include grid100gap(.5em);
  }
  &__img{
    width: 100%;
    height: 100%;
    img{
      width: 100%;
    }
  }
}

.zoom-img{
  overflow: hidden;
  width: 100%;
  height: 30.1875em;
  border-radius: 1em;
  border: 1px solid rgba(102, 112, 133, 0.33);
  &._big{
    grid-column: 1/-1;
    @media screen and (min-width: 993px){
      height: 52em;
    }
  }
}

.openseadragon-container{
  &>div{
    &:nth-child(2){
      right: .5em !important;
      bottom: .75em !important;
      left: unset !important;
      top: unset !important;
      &>div{
        opacity: 1 !important;
        &>div{
          display: flex !important;
          flex-direction: column;
        }
      }
      [title]{
        border: 1px solid $defaultBlue !important;
        margin-top: .25em !important;
        width: 2.75em;
        height: 2.75em;
        border-radius: .5em;
        background-color: #fff !important;
        background-size: 1.25em auto !important;
        background-position: center !important;
        background-repeat: no-repeat !important;
        transition: all .3s;
        cursor: pointer;
        img{
          display: none;
        }
        &:hover{
          background-color: $defaultBlue !important;
        }
      }
      [title="Zoom out"]{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.16669 10H15.8334' stroke='%230035BE' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        &:hover{
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.16669 10H15.8334' stroke='%23fff' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        }
      }
      [title="Zoom in"]{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10 4.16667V15.8333M4.16669 10H15.8334' stroke='%230035BE' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        &:hover{
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10 4.16667V15.8333M4.16669 10H15.8334' stroke='%23fff' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        }
      }
      [title="Go home"]{
        order: -1;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.24998 6.25H6.25831M17.575 11.5917L11.6 17.5667C11.4452 17.7216 11.2614 17.8446 11.059 17.9284C10.8567 18.0123 10.6398 18.0555 10.4208 18.0555C10.2018 18.0555 9.98491 18.0123 9.78258 17.9284C9.58025 17.8446 9.39644 17.7216 9.24165 17.5667L2.08331 10.4167V2.08333H10.4166L17.575 9.24167C17.8854 9.55394 18.0596 9.97636 18.0596 10.4167C18.0596 10.857 17.8854 11.2794 17.575 11.5917Z' stroke='%230035BE' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        &:hover{
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.24998 6.25H6.25831M17.575 11.5917L11.6 17.5667C11.4452 17.7216 11.2614 17.8446 11.059 17.9284C10.8567 18.0123 10.6398 18.0555 10.4208 18.0555C10.2018 18.0555 9.98491 18.0123 9.78258 17.9284C9.58025 17.8446 9.39644 17.7216 9.24165 17.5667L2.08331 10.4167V2.08333H10.4166L17.575 9.24167C17.8854 9.55394 18.0596 9.97636 18.0596 10.4167C18.0596 10.857 17.8854 11.2794 17.575 11.5917Z' stroke='white' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
        }
      }
      [title="Toggle full page"]{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_1914_654)'%3E%3Cpath d='M7.50002 15.8333L10 18.3333M10 18.3333L12.5 15.8333M10 18.3333L10 1.66666M4.16669 12.5L1.66669 9.99999M1.66669 9.99999L4.16669 7.49999M1.66669 9.99999L18.3334 9.99999M15.8334 7.49999L18.3334 9.99999M18.3334 9.99999L15.8334 12.5M7.50002 4.16666L10 1.66666M10 1.66666L12.5 4.16666' stroke='%230035BE' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_1914_654'%3E%3Crect width='20' height='20' fill='white' transform='matrix(0 -1 1 0 0 20)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E") !important;

        &:hover{
          background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_1914_654)'%3E%3Cpath d='M7.50002 15.8333L10 18.3333M10 18.3333L12.5 15.8333M10 18.3333L10 1.66666M4.16669 12.5L1.66669 9.99999M1.66669 9.99999L4.16669 7.49999M1.66669 9.99999L18.3334 9.99999M15.8334 7.49999L18.3334 9.99999M18.3334 9.99999L15.8334 12.5M7.50002 4.16666L10 1.66666M10 1.66666L12.5 4.16666' stroke='%23fff' stroke-width='1.67' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_1914_654'%3E%3Crect width='20' height='20' fill='white' transform='matrix(0 -1 1 0 0 20)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E") !important;
        }
      }
    }
  }
}

.fullpage{
  .openseadragon-container {
    & > div {
      &:nth-child(2) {
        right: 1em !important;
        bottom: 1em !important;
      }
    }
  }
}

.part-card2{
  background: #FFFFFF;
  border: 1px solid #E4E7EC;
  box-shadow: 0px 4px 8px -2px rgba(16, 24, 40, 0.1), 0px 2px 4px -2px rgba(16, 24, 40, 0.06);
  border-radius: .5em;
  padding: .5em 1em;
  &__title{
    b{
      font-weight: 700;
    }
  }
  &__table{
    padding-top: 1.5em;
    background-color: transparent;
    table{
      box-shadow: none;
    }
    th,td{
      padding: 0 .5em;
    }
    td{
      height: 2.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-parts{
    .info-block2__title{
      letter-spacing: -0.02em;
    }
  }

  .zoom-img{
    height: 20.5em;
    padding: 0;
  }

  .openseadragon-container{
    &>div{
      &:nth-child(2){
        right: .5em !important;
        bottom: .5em !important;
        [title]{
          width: 2.25em;
          height: 2.25em;
        }
      }
    }
  }

  .car-info{
    margin-top: 2em;
    padding: 1em;
    width: 100%;
    margin-left: 0;
    &__grid{
      column-count: 1;
    }
  }

  .catalog-part{
    &__wrap{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 2em;
    }
    &__right{
      order: -1;
    }
    &__items{
      @include grid100gap(.5em);
    }
  }

  .part-card2{
    &__table{

    }
    .m-table{
      td{
        grid-template-columns: 43.5% 56.5%;
      }
      &__title,
      &__desc{
        padding-left: .5em;
        padding-right: .5em;
      }
    }
  }
}
