.header{
  //position: sticky;
  //top: 0;
  position: relative;
  z-index: 10;
  background-color: #fff;
  min-height: 9em;
  &._fixed{
    .header-bottom{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}

.header-bottom{
  background-color: #fff;
}

.header-search{
  @media screen and (min-width: 993px){
    flex-grow: 1;
    max-width: 53.25em;
    input{
      border-radius: 0.5em;
    }
  }
}

.header{
  &__co{
    margin-right: .75em;
  }
}

.menu__item{
  &._active{
    .menu__item-show{
      color: $black;
    }
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
  @media screen and (min-width: 993px){
    .menu__item-show > img{
      display: none;
    }
  }
}

.catalog-icon{
  width: 1.25em;
  height: 1.25em;
  position: relative;
  flex-shrink: 0;
  margin-right: .5em;
  img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) !important;
    width: 100%;
    transition: all .3s;
    margin: 0;
  }
  ._view2{
    opacity: 0;
  }
}

.header-contacts{
  @include grid100gap(.125em);
  position: relative;
  z-index: 3;
  &._open-contacts{
    .header-contacts__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .header-contacts__btn img{
      transform: rotate(180deg);
    }
  }
  &__dropdown{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    @include grid100gap(.125em);
    position: absolute;
    right: -1.5em;
    top: 1.5em;
    min-width: 11.125em;
    border-radius: 0.5em;
    background: #FFF;
    box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.10), 0 4px 6px -2px rgba(16, 24, 40, 0.05);
    padding: .5em 1em;
    text-align: right;
  }
  &__item{
    font-size: 0.875em;
    font-weight: 500;
    line-height: 121%;
  }
  &__btn{
    position: absolute;
    top: 0;
    left: 100%;
    padding-left: .25em;
    @include center;
    img{
      width: 1.25em;
      display: block;
      transition: all .3s;
    }
  }
  &__row{
    text-align: right;
    color: #2A3640;
    a{
      font-size: 0.875em;
      font-weight: 500;
      line-height: 121%;
    }
    span{
      color: $defaultBlue;
      font-size: 0.6875em;
      font-weight: 500;
      line-height: 181%;
    }
  }
}

.header-balance{
  margin-left: 4em;
  text-align: right;
  font-weight: 500;
}


@media screen and (max-width: 992px){
  .header{
    &__co{
      display: none;
    }
  }

  .header-balance,
  .header-contacts,
  .catalog-icon{
    display: none;
  }
}