.index-menu{
  padding: 2em 0 5.5em;
  &__grid{
    width: 69em;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    border-radius: 1em;
    overflow: hidden;
  }
}

.index-menu-card{
  padding: 1.5em 1.5em 1em;
  background: linear-gradient(90deg, rgba(0, 53, 190, 0.00) 46.47%, rgba(0, 53, 190, 0.20) 100%), rgba(170, 192, 249, 0.12);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background-size: 100% auto;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: 2;
    opacity: 0;
  }
  &--catalog{
    &:before{
      width: 6.25em;
      height: 100%;
      top: 0;
      right: 0;
      opacity: 1;
      background-image: url(../img/triangle2.svg);
      z-index: 3;
    }
  }
  &--about{
    background: linear-gradient(180deg, rgba(0, 53, 190, 0.00) 50%, rgba(0, 53, 190, 0.20) 100%), rgba(170, 192, 249, 0.12);
    &:before{
      width: 100%;
      height: 2.5em;
      bottom: 0;
      right: 0;
      opacity: 1;
      background-image: url(../img/triangle4.svg);
      z-index: 3;
      background-size: 100% auto;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    img{
      width: 3em;
      margin-right: 1em;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 167%;
    font-weight: 500;
  }
  &__links,
  &__links2{
    margin-top: 1.5em;
    font-weight: 500;
    color: $defaultBlue;
  }
  &__links{
    @include grid100gap(.25em);
    a{
      padding: .75em;
      display: flex;
      align-items: center;
      &:hover{
        opacity: .8;
      }
    }
    img{
      width: 1.5em;
      margin-right: 1em;
    }
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
  &__links2{
    @include grid100gap(1em);
    a{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
}

.index-menu-img{
  padding-bottom: 100%;
  min-height: 100%;
  position: relative;
  background: $defaultBlue;
  &:before{
    content: '';
    position: absolute;
    background-size: 100% auto;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: 2;
    opacity: 0;
  }
  img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    max-width: 85%;
    max-height: 85%;
  }
  &--catalog{
    &:before{
      width: 100%;
      height: 2.5em;
      left: 0;
      bottom: 0;
      opacity: 1;
      background-image: url(../img/triangle.svg);
    }
  }
  &--about{
    background: #132E75;
    z-index: 3;
    &:before{
      width: 3.125em;
      height: 100%;
      right: 0;
      bottom: 0;
      opacity: 1;
      background-image: url(../img/triangle3.svg);
    }
  }
  &--info{
    background: #446BCF;
  }
}


@media screen and (max-width: 992px){
  .index-menu{
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
      border-radius: 1em;
    }
  }

  .index-menu-card,
  .index-menu-img{
    &:before{
      display: none;
    }
  }

}


@media screen and (max-width: 576px){
  .index-menu-card{
    font-size: .75em;
    padding-bottom: 1.5em;
  }
}